#news {
	width: 100%;
	height: 100vh;
	background: skyblue;
	display: flex;
	justify-content: center;
	align-items: center;
	gap: 30px;

	article {
		width: 15%;
		height: 40%;
		background: #fff;
		transform: translateY(100px) rotateY('120deg');
		opacity: 0;
		transition: 0.5s;

		@for $i from 1 through 4 {
			&:nth-of-type(#{$i}) {
				transition-delay: ($i - 1) * 0.2s;
			}
		}
	}
}
#news.on {
	article {
		transform: translateY(0) rotateY('0');
		opacity: 1;
	}
}
